import SectionTitle from 'components/ui/SectionTitle';
import SectionWrap from 'components/sections/SectionWrap';
import PopularSearchKeyword from 'components/ui/PopularSearchKeyword';
import styled from 'styled-components';

export default function () {
  const lists = [
    { keyword: '  ', status: 'up', rank: 1 },
    { keyword: ' ', status: 'up', rank: 2 },
    { keyword: ' ', status: 'up', rank: 3 },
    { keyword: ' ', status: 'down', rank: 4 },
    { keyword: ' ', status: 'up', rank: 5 },
    { keyword: '', status: 'new', rank: 6 },
    { keyword: '', status: 'up', rank: 7 },
    { keyword: ' ', status: 'up', rank: 8 },
    { keyword: ' ', status: 'down', rank: 9 },
    { keyword: ' ', status: 'new', rank: 10 },
  ];

  return (
    <section>
      <SectionWrap>
        <SectionTitle title={``} />
        <div>
          <List>
            {lists.map((item) => {
              return (
                <Item>
                  <PopularSearchKeyword item={item} />
                </Item>
              );
            })}
          </List>
        </div>
      </SectionWrap>
    </section>
  );
}

const List = styled.ol`
  display: grid;
  grid-template-columns: 1fr 1fr;
`;

const Item = styled.li`
  min-width: 0;
`;
