import SectionTitle from 'components/ui/SectionTitle';
import SectionWrap from 'components/sections/SectionWrap';
import BestItem from './BestItem';
import styled from 'styled-components';
import useFetch from 'hooks/use-fetch';
import { useState } from 'react';
import ProductCategoryControlSlide from './ProductCategoryControlSlide';
import { v4 as uuidv4 } from 'uuid';

const categoryList = [
  { id: uuidv4(), all: '' },
  { id: uuidv4(), christmas: ' ' },
  { id: uuidv4(), furniture: '' },
  { id: uuidv4(), fabrics: '' },
  { id: uuidv4(), homeAppliances: '' },
  { id: uuidv4(), kitchenware: '' },
  { id: uuidv4(), food: '' },
  { id: uuidv4(), decor: '' },
  { id: uuidv4(), lighting: '' },
  { id: uuidv4(), storage: '' },
  { id: uuidv4(), householdGoods: '' },
  { id: uuidv4(), dailyNecessities: '' },
  { id: uuidv4(), kids: '' },
  { id: uuidv4(), pets: '' },
  { id: uuidv4(), camping: '' },
  { id: uuidv4(), tools: 'DIY' },
  { id: uuidv4(), interior: '' },
  { id: uuidv4(), rental: '' },
  { id: uuidv4(), grocery: '' },
  { id: uuidv4(), wedding: '' },
];

export default function SectionBestProduct() {
  const [data, loading, error] = useFetch('best-products');
  const [category, setCategory] = useState('all');

  const handleCategory = (e) => {
    const value = e.target.textContent;
    const obj = categoryList.find((v) => {
      return Object.values(v)[1] === value;
    });
    const key = Object.keys(obj)[1];
    setCategory(key);
  };

  return (
    <section>
      <SectionWrap>
        <SectionTitle title={''} />
        <div>
          <ProductCategoryControlSlide
            categoryList={categoryList}
            handleCategory={handleCategory}
            category={category}
          />
          <div>
            <Group>
              {data[category] &&
                data[category].map((item) => {
                  return (
                    <BestItem
                      key={item.id}
                      cssName={'best-product'}
                      item={item}
                    />
                  );
                })}
            </Group>
          </div>
        </div>
      </SectionWrap>
    </section>
  );
}

const Group = styled.div`
  display: flex;
  gap: 12px;
`;
