import { useEffect, useState } from 'react';
import styled from 'styled-components';
import { textStyle } from 'styles/mixins/text-style';
import { MD } from 'styles/mixins/styled-responsive';
import { HideMobile } from 'styles/react-responsive';
import { v4 as uuidv4 } from 'uuid';

export default function HeaderLnb({ gnb }) {
  const [category, setCategory] = useState('community');

  useEffect(() => {
    if (gnb === '') {
      setCategory('community');
    } else if (gnb === '') {
      setCategory('shopping');
    } else if (gnb === '/') {
      setCategory('interior');
    } else {
      setCategory('community');
    }
  }, [gnb]);

  const lists = {
    community: [
      '',
      '',
      '#',
      '',
      '',
      '3D',
      '',
      '',
      '',
      '',
      '',
      '',
      '',
      '',
      '',
      '',
    ],
    shopping: [
      '',
      '',
      '',
      '',
      '',
      '',
      '',
      '!',
      '',
      '',
    ],
    interior: [
      '',
      '',
      '',
      '',
      '',
      '',
      '',
      '',
    ],
  };

  const updateLists = Object.keys(lists).reduce((acc, key) => {
    acc[key] = lists[key].map((item) => ({ id: uuidv4(), text: item }));
    return acc;
  }, {});

  return (
    <StyledHeaderLnb>
      <List>
        {updateLists[category].map((list) => {
          return (
            <Item key={list.id}>
              <a href="">{list.text}</a>
            </Item>
          );
        })}
      </List>
      <HideMobile>
        <Button type="button" aria-label=" "></Button>
      </HideMobile>
    </StyledHeaderLnb>
  );
}

const StyledHeaderLnb = styled.nav`
  width: 100%;
  height: 40px;

  &::after {
    position: absolute;
    top: 1px;
    right: 0;
    bottom: 1px;
    display: block;
    width: 100px;
    cursor: pointer;
    background: linear-gradient(
      to right,
      #ffffff00 0%,
      rgb(255, 255, 255, 50) 50%
    );
    content: '';
  }
  ${MD`
    position:relative;
    display:flex;
    justify-content : space-between;
    height:50px;
  `}
`;

const List = styled.ul`
  display: flex;
  height: 100%;
  overflow-x: scroll;
  overflow-y: hidden;

  ${MD`
      overflow: hidden;
      gap: 12px;
  `}
`;

const Item = styled.li`
  a {
    ${textStyle(14)}
    display: block;
    padding: 11px 5px;
    font-weight: 700;
    white-space: nowrap;
  }

  ${MD`
    position: relative;
      display: flex;
      align-items: center;
      height: 100%;

      a {
          ${textStyle(16)}
        padding: 13px 6px;
      }

  `}
`;

const Button = styled.button`
  position: relative;
  z-index: 1;
  flex-shrink: 0;
  width: 36px;
  cursor: pointer;

  &::after {
    position: absolute;
    top: 37%;
    left: 50%;
    display: block;
    width: 6px;
    height: 6px;
    border-right: 1px solid ${({ theme }) => theme.colors.fontGray1};
    border-bottom: 1px solid ${({ theme }) => theme.colors.fontGray1};
    content: '';
    transform: translateX(-50%) rotate(45deg);
  }
`;
