import imageInhasa from 'assets/interior_review_inhasa.jpg';
import imageDamso from 'assets/interior_damso.jpg';
import imageWoogin from 'assets/interior_woojin.jpg';
import imageMoInhasa from 'assets/mo_interior_review_inhasa.jpg';
import imageMoDamso from 'assets/mo_interior_damso.jpg';
import imageMoWoogin from 'assets/mo_interior_woojin.jpg';
import SectionTitle from 'components/ui/SectionTitle';
import SectionWrap from 'components/sections/SectionWrap';
import SectionMoreLink from 'components/ui/SectionMoreLink';
import ReviewItem from './ReviewItem';
import styled from 'styled-components';
import { MD } from 'styles/mixins/styled-responsive';
import { v4 as uuidv4 } from 'uuid';

export default function SectionInteriorContruction() {
  const lists = [
    {
      id: uuidv4(),
      title: '',
      desc: '                                           ?             ! 25                                     !      ,    .       .  !',
      imageSrc: imageInhasa,
      imageMoSrc: imageMoInhasa,
    },

    {
      id: uuidv4(),
      title: '',
      desc: '            +                       ~~',
      imageSrc: imageDamso,
      imageMoSrc: imageMoDamso,
    },
    {
      id: uuidv4(),
      title: '',
      desc: '                                   .             .               .             ',
      imageSrc: imageWoogin,
      imageMoSrc: imageMoWoogin,
    },
  ];

  return (
    <section>
      <SectionWrap>
        <SectionTitle title={`   `} />
        <div>
          <ReviewList>
            {lists.map((item) => {
              return (
                <ReviewItem
                  key={item.id}
                  item={item}
                  cssName={'interior-construction'}
                />
              );
            })}
          </ReviewList>

          <SectionMoreLink text={'  '} />
        </div>
      </SectionWrap>
    </section>
  );
}

const ReviewList = styled.div`
  display: flex;
  flex-direction: column;
  gap: 20px;

  ${MD`
      flex-direction: row;
      gap: 16px;
  `}
`;
